/* Page styling
================================== */
@import url(https://fonts.googleapis.com/css?family=Josefin+Sans:400,700);
html {
  height: 100%;
  background-color: #EEE; }

body {
  position: relative;
  box-sizing: border-box;
  min-height: 100%;
  margin: 0 auto;
  padding: 2em;
  max-width: 800px;
  font-family: 'Josefin Sans', sans-serif;
  font-size: 1.2em;
  background-color: white;
  border: double 3px #DDD;
  border-top: none;
  border-bottom: none; }

#root {
  overflow-y: hidden;
  padding-bottom: 100px; }

h1, h2, h3, h4, h5, h6 {
  margin: 0; }

h3 {
  font-size: 1.2em; }

h4 {
  font-size: 1em; }

strong {
  color: #434d57; }

.btn {
  border: none;
  background-color: #cbcbcb;
  cursor: pointer;
  height: 25px;
  border-radius: 2px;
  letter-spacing: 1px;
  text-transform: uppercase;
  outline: none;
  transition: all 250ms; }
  .btn:hover {
    background-color: #909090;
    color: #f9f9f9;
    letter-spacing: 2px;
    font-weight: bold; }

input.default {
  border: none;
  border-bottom: 1px solid #cbcbcb;
  outline: none;
  transition: all 250ms; }
  input.default:active, input.default:focus {
    border-color: #005aff; }

header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px; }

form {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px; }
  form label {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px; }
    form label span, form label input {
      flex: 1;
      padding-right: 10px; }
  form.conversion {
    position: absolute;
    bottom: 2em;
    right: 2em;
    margin-bottom: 0;
    width: 400px;
    max-width: calc(100% - 4em); }

.expenses {
  overflow-x: auto; }
  .expenses table {
    width: 100%;
    margin-bottom: 20px; }
    .expenses table tbody tr:nth-child(odd) {
      background-color: #cbcbcb; }
    .expenses table tbody tr:first-child {
      background-color: #ababab; }
    .expenses table tbody tr th, .expenses table tbody tr td {
      padding: 5px; }
      .expenses table tbody tr th.delete, .expenses table tbody tr td.delete {
        cursor: pointer;
        transition: background-color 250ms; }
        .expenses table tbody tr th.delete:hover, .expenses table tbody tr td.delete:hover {
          background-color: #ababab; }

.summary {
  margin-bottom: 20px; }

